import { Typography, useTheme } from "@stardust-ds/react";
import useStore from "hooks/useStore";
import { shallow } from "zustand/shallow";
import { Container } from "./styles"

const Users: React.FC = () => {

  const theme = useTheme();
  const { user } = useStore(state => 
    ({
      user: state.user
    }), shallow);

    return (
      <Container>
        <div>
          <Typography color={theme.brand.color.primary.medium} mBottom='md' fontSize='lg'>
            Usuários
          </Typography>
          <Typography color={theme.brand.color.primary.medium} fontSize='xs'>
            Usuário: {user.name}
          </Typography>
        </div>
      </Container>
    );
  }
  
  export default Users;